.content-wrapper {
  --breakout-size: calc(
    (var(--content-breakout-max-width) - var(--content-max-width)) / 2
  );

  display: grid;

  grid-template-columns:
    [full-width-start] minmax(var(--s-sides-inset), 1fr)
    [breakout-start] minmax(0, var(--breakout-size))
    [content-start] min(
      100% - (var(--s-sides-inset) * 2),
      var(--content-max-width)
    )
    [content-end]
    minmax(0, var(--breakout-size)) [breakout-end]
    minmax(var(--s-sides-inset), 1fr) [full-width-end];
}

.content-wrapper > :not(.escape-wrapper-breakout, .escape-wrapper-full-width) {
  grid-column: content;
}

.content-wrapper > .escape-wrapper-breakout {
  grid-column: breakout;
}

.content-wrapper > .escape-wrapper-full-width {
  grid-column: full-width;

  display: grid;
  /* grid-template-columns: inherit; */
}
