$breakpoints: (
  'xs': 410px,
  'sm': 640px,
  'md': 768px,
  'lg': 1024px,
  'xl': 1280px,
  'xxl': 1536px,
);

@mixin xs {
  @media all and (min-width: map-get($breakpoints, 'xs')) {
    @content;
  }
}

@mixin sm {
  @media all and (min-width: map-get($breakpoints, 'sm')) {
    @content;
  }
}

@mixin md {
  @media all and (min-width: map-get($breakpoints, 'md')) {
    @content;
  }
}

@mixin lg {
  @media all and (min-width: map-get($breakpoints, 'lg')) {
    @content;
  }
}

@mixin xl {
  @media all and (min-width: map-get($breakpoints, 'xl')) {
    @content;
  }
}
@mixin xl {
  @media all and (min-width: map-get($breakpoints, 'xxl')) {
    @content;
  }
}
